import React, { useRef, useState } from "react"
import { PickerView } from "./picker"

interface MainViewProps {
    prefixes: string;
    suffixes: string;
}

export function MainView(props: MainViewProps): JSX.Element {
    const [prefix, setPrefix] = useState(" ");
    const [suffix, setSuffix] = useState(" ");

    const prefixRef: React.MutableRefObject<any> = useRef(null);
    const suffixRef: React.MutableRefObject<any> = useRef(null);

    const random = () => {
        if (prefixRef.current) {
            prefixRef.current.click();
        }

        if (suffixRef.current) {
            suffixRef.current.click();
        }
    }

    return (
        <div className="main">
            <p>{prefix}{suffix}</p>
            <PickerView title="Prefix" uid="pid" path={props.prefixes} onNameChange={setPrefix} ref={prefixRef}></PickerView>
            <PickerView title="Suffix" uid="sid" path={props.suffixes} onNameChange={setSuffix} ref={suffixRef}></PickerView>
            <button className="randomBtn" onClick={random}>Random</button>
        </div>
    );
}
